/* Woocommerce Product Page shortcode style - begin
========================================================================== */

div.woocommerce {

    > .single-product {

        .images {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0;
            padding: 0;

            a {
                @include eltdfRelativeHolderLayout();

                img {
                    position: relative;
                    width: 100%;
                    display: block;
                }
            }

			figure {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 -10px;
			}

			.woocommerce-product-gallery__image {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 10px;
				box-sizing: border-box;

				&:first-child {
					width: 100%;
				}
			}

			&.woocommerce-product-gallery--with-images {

				.woocommerce-product-gallery__image {

					&:not(:first-child) {
						float: left;
						padding-top: 20px;
					}
				}
			}

			$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
			@for $i from 0 to length($thumb_3_columns_label) {
				&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
					$column_width: 100%/($i+2);

					.woocommerce-product-gallery__image {

						&:not(:first-child) {
							width: $column_width;
						}
					}
				}
			}
        }

        .woocommerce-tabs {
            @include eltdfRelativeHolderLayout();
            margin: 54px 0 0;

            h2 {
                font-size: 14px;
                margin: 16px 0 13px;
            }

            ul.tabs {
                @include eltdfRelativeHolderLayout();
                list-style: none;
                margin: 0;
                padding: 0;
                clear: both;

                > li {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    float: left;

                    a {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                        padding: 0 9px 11px;
                        color: $default-text-color;
                    }

                    &:hover,
                    &.active {

                        a {
                            color: $default-heading-color;
                        }
                    }
                }
            }

            .entry-content {
                padding: 18px 0;
            }

            table {
                margin: 0 0 20px;

                tr, th {
                    border: 0;
                }

                td, th {
                    padding: 3px 0;
                    vertical-align: middle;
                    text-align: left;
                }

                th {
                    color: $default-heading-color;
                    width: 1%;
                    padding: 0 10px 0 0;
                }

                td {
                    width: 99%;
                }
            }

            #reviews {

                ol.commentlist {
                    list-style: none;

                    li {
                        padding-top: 10px;

                        &:first-child {
                            padding: 0;
                        }
                    }

                    .comment_container {
                        @include eltdfRelativeHolderLayout();

                        img {
                            float: left;
                            width: 60px;
                        }

                        .comment-text {
                            padding: 0 0 0 80px;

                            .star-rating {
                                margin: 0;
                            }

                            p {
                                display: block;
                                margin: 5px 0 0;
                            }
                        }
                    }
                }

                .comment-respond {
                    margin-top: 15px;

                    .comment-reply-title {
                        margin: 0 0 10px;
                    }

                    .stars {
                        overflow: hidden;
                        padding: 0;
                        margin: 0;
                    }

                    .stars a {
                        position: relative;
                        display: inline-block;
                        color: transparent;
                        font-size: 0;
                        margin: 0;

                        &:before {
                            position: relative;
                            display: inline-block;
                            vertical-align: middle;
                            font-family: "ElegantIcons";
                            content: "\e031";
                            font-size: 12px;
                            line-height: 1;
                            letter-spacing: 3px;
                            color: $default-heading-color;
                            overflow: hidden;
                            z-index: -1;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }

                        &.active:after {
                            position: absolute;
                            right: 0;
                            bottom: 6px;
                            display: block;
                            font-family: "ElegantIcons";
                            font-size: 12px;
                            line-height: 1;
                            letter-spacing: 3px;
                            color: $default-heading-color;
                            content: "\e033\e033\e033\e033\e033";
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    .comment-form-comment label {
                        display: block;
                        margin: 0 0 10px;
                    }
                }
            }
        }

        .related.products,
        .upsells.products {

            > h2 {
                display: block;
                margin: 0 0 57px;
                font-size: 25px;
                text-align: center;
            }
        }

        .related.products {
            margin: 12px 0 10px;
        }

        .upsells.products {
            margin: 12px 0 40px;
            border-bottom: 1px solid $default-border-color;
        }
    }
}

/* Woocommerce Product Page shortcode style - end
========================================================================== */