/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .eltdf-onsale,
    .eltdf-out-of-stock {
        position: absolute;
        display: block;
        top: 0;
        width: auto;
        height: 24px;
        color: #fff;
        font-size: 8px;
        line-height: 24px;
        letter-spacing: 1px;
        padding: 0 16px;
        margin: 0;
        text-align: center;
        z-index: 100;
        @include eltdfTransform(translateZ(0));
        box-sizing: border-box;
    }
    
    .eltdf-onsale {
        left: 0;
        background-color: $first-main-color;
    }
    
    .eltdf-out-of-stock {
        right: 0;
        background-color: rgba($default-heading-color, .5);
		padding: 0 21px;
    }
}

.eltdf-woo-single-page {
    
    .eltdf-single-product-content {
        
        .eltdf-out-of-stock {
            right: 10px;
        }
    }
    
    &.eltdf-woo-single-thumb-on-left-side {
        
        .eltdf-single-product-content {
            
            .eltdf-onsale {
                left: 120px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */