/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
	clear: both;

	&.widget_rating_filter,
	&.widget_shopping_cart,
	&.widget_layered_nav,
	&.widget_price_filter,
	&.widget_layered_nav_filters,
	&.widget_product_categories,
	&.widget_products,
	&.widget_product_search,
	&.widget_product_tag_cloud,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		margin: 0 0 30px;
	}

	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {

		.eltdf-widget-title {
			margin: 0 0 32px;
		}
	}

	&.widget_rating_filter,
	&.widget_shopping_cart,
	&.widget_layered_nav,
	&.widget_layered_nav_filters,
	&.widget_product_categories,
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products  {

		ul {
			padding: 0;
			margin: 0;
			list-style: none;

			li {
				@include eltdfRelativeHolderLayout();
				padding: 0;
				margin: 0 0 4px;
				clear: both;

				&:last-child {
					margin: 0;
				}

				ul {
					margin: 4px 0 0 10px;
				}
			}
		}
	}

	&.widget_shopping_cart {

		.widget_shopping_cart_content {
			@include eltdfRelativeHolderLayout();

			ul {

				li {

					img {
						display: block;
						max-width: 94px;
						margin: 0 70% 21px 0;
					}

					a:not(.remove) {
						font-size: 18px;
						font-weight: 700;
					}

					.remove {
						position: absolute;
						top: 0;
						right: 0;
						font-size: 0;
						line-height: 1;

						&:after {
							position: relative;
							display: block;
							padding: 0 3px;
							font-family: 'Ionicons';
							content: "\f2d7";
							font-size: 20px;
							line-height: 1em;
						}
					}
				}
			}

			.variation {
				@include eltdfRelativeHolderLayout();

				> * {
					display: inline-block;
					vertical-align: middle;
					margin: 0;

					p {
						margin: 0;
					}
				}
			}

			.total {
				margin: 7px 0 22px;
			}

			.buttons {
				margin: 10px 0 0;
			}
		}
	}

	&.widget_layered_nav {

		ul {

			li {

				&.chosen {

					a {
						color: $first-main-color;
					}
				}
			}
		}
	}

	&.widget_price_filter {

		.eltdf-widget-title {
			margin: 0 0 25px;
		}

		.price_slider_wrapper {

			.ui-widget-content {
				position: relative;
				height: 10px;
				background-color: #f4f4f4;

				.ui-slider-handle {
					position: absolute;
					top: -3px;
					height: 16px;
					width: 16px;
					margin: 0 0 0 -1px;
					background-color: #f6f6f6;
					box-shadow: 0 3px 3px 0 rgba(#000, .15);
					border-radius: 100%;
					cursor: pointer;
					outline: medium none;
					z-index: 2;

					&:last-child {
						@include eltdfTransform(translateX(-50%));
					}
				}

				.ui-slider-range {
					position: absolute;
					display: block;
					height: 100%;
					background-color: $first-main-color;
					z-index: 1;
				}
			}
		}

		.price_slider_amount {
			margin: 20px 0 0;

			.button {
				float: right;
				height: 38px;
				line-height: 35px;
				padding: 0 25px;
			}

			.price_label {
				float: left;
				line-height: 38px;
			}
		}
	}

	&.widget_product_categories {

		.eltdf-widget-title {
			margin: 0 0 20px;
		}

		ul {

			li {

				a {
					display: block;
					padding: 0 10px 0 0;
					color: $default-text-color;
					box-sizing: border-box;
					@include eltdfTransition(color .3s ease-in-out);

					&:hover {
						color: $first-main-color;
					}
				}
			}
		}
	}

	&.widget_top_rated_products,
	&.widget_products {

		.product-title,
		.woocommerce-Price-amount {
			font-family: $default-heading-font;
			font-size: 16px;
		}
	}

	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {

		ul {

			li {
				margin: 0 0 17px;
				min-height: 102px;

				&:last-child {
					margin: 0;
				}

				a {
					font-size: 18px;
					font-weight: 700;

					img {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						max-width: 94px;
					}

					span {
						position: relative;
						display: block;
						padding: 0 0 0 114px;
						line-height: 20px;
						box-sizing: border-box;
					}
				}

				> *:not(a) {
					padding: 0 0 0 114px;
					line-height: 20px;
				}

				.star-rating {
					position: relative;
					padding: 0;
					margin: 0 0 0 114px;

					span {
						max-width: 100px;
					}
				}

				del,
				ins {
					display: block;

					span {
						color: inherit !important;
						margin: 0 !important;
					}
				}

				del {
					padding: 0 8px 0 114px;
				}

				ins {
					text-decoration: none;
				}

				.woocommerce-Price-amount.amount {
					margin-top: 11px;
				}
			}
		}
	}

	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_top_rated_products {

		ul {

			li {

				.product-title {
					color: inherit;
				}
			}
		}
	}

	&.widget_products {

		ul {

			li {

				del,
				ins {
					padding-top: 11px;
				}

				.woocommerce-Price-amount.amount {
					display: block;
				}
			}
		}
	}

	&.widget_recently_viewed_products {

		ul {

			li {

				del,
				.woocommerce-Price-amount.amount {
					display: block;
					margin-top: 11px;
				}
			}
		}
	}

	&.widget_recent_reviews {

		ul {

			li {

				.star-rating {
					margin-top: 9px;
					margin-bottom: 7px;
				}
			}
		}

		a {
			display: block;
			padding: 0 0 0 114px;
		}

		.reviewer {
			display: inline-block;
			vertical-align: middle;
			width: auto;
			clear: both;
		}
	}

	&.widget_top_rated_products {

		ul {

			li {

				.star-rating {
					margin-top: 12px;
					margin-bottom: 12px;
				}

				.woocommerce-Price-amount.amount {
					display: block;
					margin-top: 0;
				}
			}
		}
	}

	&.widget_product_tag_cloud {

		.eltdf-widget-title {
			margin: 0 0 32px;
		}

		.tagcloud {

			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 3px 7px 0;
				padding: 0 22px;
				color: $default-heading-color;
				font-size: 10px !important;
				line-height: 39px;
				font-weight: 500;
				letter-spacing: 2px;
				text-transform: uppercase;
				background-color: #f4f4f4;
				@include eltdfTransition(color .2s ease-out, background-color .2s ease-out);

				&:hover {
					color: #fff;
					background-color: $first-main-color;
				}
			}
		}
	}

	&.widget_product_search {

		.woocommerce-product-search {
			@include eltdfRelativeHolderLayout();
			clear: both;

			div.input-holder {
				@include eltdfRelativeHolderLayout();
				border-width: 1px;
				border-style: solid;
				border-color: $first-main-color;
				box-sizing: border-box;
			}

			input,
			button {
				position: relative;
				display: inline-block;
				vertical-align: top;
				float: left;
				height: 52px;
				font-family: inherit;
				font-weight: 400;
				font-size: 15px;
				line-height: 52px;
				color: $first-main-color;
				background-color: transparent;
				border: 0;
				border-radius: 0;
				-webkit-border-radius:0;
				outline: 0;
				-webkit-appearance: none;
				box-sizing: border-box;
				@include eltdfTransition(color .2s ease-out);
			}

			input {
				font-family: $default-heading-font;
				font-size: 14px;
				text-transform: uppercase;
				width: calc(100% - 40px);
				padding: 0 0 0 21px;
				border-radius:100px 0 0 100px;
				-webkit-border-radius:100px 0 0 100px;

				@include eltdfPlaceholder {
					color: $first-main-color;
				}
			}

			button {
				width: 40px;
				padding: 0 30px 0 9px;
				font-size: 13px;
				cursor: pointer;
				color: $first-main-color;
				@include eltdfTransition(color .2s ease-in-out);

				span {
					display: block;
					line-height: inherit;

					&:before {
						display: block;
						line-height: inherit;
					}
				}
			}
		}
	}
}

/* Default search widget adaptation */
.woocommerce {

	.widget_search {

		#searchsubmit {
			position: relative;
			display: inline-block;
			vertical-align: top;
			float: left;
			font-family: inherit;
			font-weight: 400;
			line-height: 52px;
			color: $first-main-color;
			background-color: transparent;
			border-radius: 0;
			width: 40px;
			height: 44px;
			padding: 0 30px 0 9px;
			font-size: 13px;
			border: 0;
			text-align: center;
			cursor: pointer;
			-webkit-border-radius:0;
			outline: 0;
			-webkit-appearance: none;
			box-sizing: border-box;
			@include eltdfTransition(color .2s ease-out);
			@include eltdfTransition(color .2s ease-in-out);

			span {
				display: block;
				line-height: inherit;

				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */