/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
	vertical-align: middle;
	height: 100%;
	padding: 0 21px;
	font-size: 16px;

    .touch & {
        cursor: pointer;
    }

    &.eltdf-cart-info-is-disabled {

        .eltdf-header-cart {

            .eltdf-cart-info {
                display: none;
            }
        }
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-size: 20px;
            line-height: 1em;

            &:before {
                display: block;
                line-height: inherit;
            }

			.eltdf-cart-info-number {
				position: absolute;
				top: -0.5em;
				background-color: $first-main-color;
				width: 13px;
				height: 13px;
				border-radius: 100%;
				right: -.75em;
				font-size: 10px;
                line-height: 13px;
				text-align: center;
				color: #fff;

				.eltdf-info-number-inner {
					display: block;
					line-height: inherit;
				}
			}
        }

		.eltdf-cart-count {
			position: absolute;
			display: inline-block;
			top: -3px;
			right: -8px;
			width: 14px;
			height: 14px;
			font-size: 11px;
			line-height: 14px;
			color: #fff;
			background-color: $first-main-color;
			border-radius: 100%;
			text-align: center;
			@include eltdfTransition(background-color .2s ease-out);
		}

        &:hover {
            color: #eac668;
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            max-height:1500px;
            opacity:1;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 308px;
    overflow:hidden;
    max-height:0;
    opacity:0;
    @include eltdfTransition(max-height 1.5s ease-out, opacity 0.1s ease-out);
	font-family: $default-heading-font;
	color: $default-text-color;
    background-color: #fff;
    border: 1px solid $default-border-color;
    text-align: left;
    z-index: 1000;
    box-sizing: border-box;

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include eltdfTableLayout;
            padding: 20px 0;
            border-bottom: 1px solid $default-border-color;
            box-sizing: border-box;

            &.eltdf-empty-cart {
                border: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
			line-height: 1.2;
        }

        .remove {
            position: absolute;
            top: 0;
            right: 3px;
            width: 18px;
            height: 18px;
            color: $default-heading-color;
	        font-size: 26px;
	        line-height: 24px;
            text-align: center;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }

            &:hover {
                color: $first-main-color;
            }
        }

        .eltdf-quantity {
			display: block;
			font-size: 14px;
			margin: 7px 0 4px;
        }

        .amount {
            display: block;
			font-size: 14px;
            line-height: 1;
        }
    }

    .eltdf-cart-bottom {
        padding: 21px 0 0;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 16px;

            > * {
                display: table-cell;
                vertical-align: top;
                line-height: 20px;
            }

            .eltdf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .eltdf-cart-description {
            @include eltdfRelativeHolderLayout();
            margin: 0 0 10px;

            .eltdf-cart-description-inner {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                padding: 17px 30px;
                background-color: #f4f4f4;
                text-align: center;
                box-sizing: border-box;

                span {
                    position: relative;
                    display: table-cell;
                    width: 100%;
                    height: 100%;
                    vertical-align: middle;
                    font-size: 12px;
                    line-height: 18px;
                }
            }
        }

        .eltdf-view-cart {
            @include eltdfRelativeHolderLayout();
            height: 44px;
            line-height: 44px;
            padding: 0 26px;
			font-family: $default-heading-font;
            font-size: 11px;
            font-weight: inherit;
	        letter-spacing: 2px;
            color: #fff;
            background-color: $first-main-color;
            border: 0;
            outline: none;
            cursor: pointer;
            text-align: center;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */