/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();

	&.eltdf-info-below-image {

		.eltdf-pli-text {

			.eltdf-pli-text-inner {
				@include eltdfTransition(all .4s cubic-bezier(.17,.78,.38,.89));
				@include eltdfTransform(translateY(-20px));
			}
		}
	}

    .eltdf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .eltdf-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .eltdf-pli-category {
			font-family: $default-heading-font;
			font-size: 16px;
			margin: 4px 0 3px;

			a {
				color: $first-main-color;

				&:hover {
					color: $default-heading-color;
				}
			}
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .eltdf-pli-rating {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    font-family: "ElegantIcons";
		    font-size: 12px;
		    line-height: 1;
		    letter-spacing: 2px;
		    color: $first-main-color;
		    overflow: hidden;
		    -webkit-font-smoothing: antialiased;

		    &:before {
			    content: "\e031\e031\e031\e031\e031";
		    }

		    span {
			    position: absolute;
			    top: 0;
			    left: 0;
			    overflow: hidden;

			    &:before {
				    content: "\e033\e033\e033\e033\e033";
			    }
		    }
	    }

        .eltdf-pli-price {
			font-family: $default-heading-font;
			font-size: 16px;
            line-height: 1;
            margin: 10px 0 0;
            color: $default-heading-color;

            * {
                font-size: inherit;
                line-height: inherit;
            }

            del {
				color: $default-text-color;
	            margin: 0 8px 0 0;
            }

            ins {                
                text-decoration: none;
            }
        }
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        &:hover {

            .eltdf-pli-text {
                opacity: 1;

				.eltdf-pli-text-inner {
					@include eltdfTransform(none);
				}
            }

			.eltdf-pli-image {

				img {
					@include eltdfTransform(scale(1.05,1.05));
				}
			}
        }

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
                will-change:transform;
				@include eltdfTransition(all 0.32s cubic-bezier(0.45, 0.05, 0.55, 0.95));
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock {
                position: absolute;
                display: block;
                top: 0;
                width: auto;
                height: 44px;
				font-family: $default-heading-font;
                font-size: 11px;
                line-height: 44px;
                letter-spacing: 1px;
				color: #fff;
                padding: 0 16px;
                margin: 0;
                text-align: center;
                z-index: 100;
                @include eltdfTransform(translateZ(0));
                box-sizing: border-box;
            }

            .eltdf-pli-onsale {
                left: 0;
	            background-color: $first-main-color;
            }

            .eltdf-pli-out-of-stock {
	            right: 0;
	            background-color: rgba($default-heading-color, .5);
            }
        }
            
        .eltdf-pli-text {
            position: absolute;
            top: 0;
            left: 0;
            display: inline-block;
            width: 100%;
            height: 100%;
            padding: 10px;
            opacity: 0;
            background-color: rgba($first-main-color, 0.9);
            box-sizing: border-box;
            @include eltdfTransition(all .3s);
        }

        .eltdf-pli-text-outer {
            position: relative;
            display: table;
            width: 100%;
            height: 100%;
        }

        .eltdf-pli-text-inner {
            position: relative;
            display: table-cell;
            width: 100%;
            height: 100%;
            vertical-align: middle;

            .eltdf-pli-title,
            .eltdf-pli-category,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating {
                color: #fff;
            }

            .eltdf-pli-price {
                color: #fff;

                del {
                    color: #ccc;
                }
            }

	        .eltdf-pli-add-to-cart {
				text-align: center;
				margin: 8px 0 0;

				.button,
				.added_to_cart {
					font-family: $default-heading-font;
					font-size: 16px;
					color: #fff;
					padding: 10px;

					&:after {
						content: " ";
						position: absolute;
						display: block;
						left: 0;
						bottom: 0;
						width: 100%;
						border-bottom: 1px solid #fff;
						opacity: 0;
						@include eltdfTransition(opacity 0.2s ease-in);
					}

					&:hover {
						color: #fff;

						&:after {
							opacity: 1;
						}
					}
				}

		        &.eltdf-light-skin {

			        .button,
			        .added_to_cart {
						font-size: 16px;
				        color: $default-heading-color;

						&:hover {
							color: #fff;
						}
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include eltdfButtonDefaultStyle();
	            @include eltdfButtonMediumParams();
				padding: 13px 5px;
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .eltdf-pli-link {
		    position: absolute;
		    top: 0;
		    left: 0;
		    display: inline-block;
		    width: 100%;
		    height: 100%;
	    }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 22px 0 38px;
        text-align: left;
    }
	
	&.eltdf-product-info-light {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating {
					color: #fff;
				}
				
				.eltdf-pli-price {
					color: #fff;
					
					del {
						color: #ccc;
					}
				}
			}
		}
	}
	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating {
					color: $default-heading-color;
				}
				
				.eltdf-pli-price {
					color: $default-heading-color;
				}
			}
		}
	}
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal');
	$columns_space_width: (0, 5, 10, 15);
	
	@for $i from 0 to length($columns_space_label) {
		&.eltdf-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space_width,$i+1);
			$column_width_margin: $column_width * 2;
			
			.eltdf-pl-outer {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}
				
				.eltdf-pli {
					padding: 0 #{$column_width}px;
					margin: 0 0 #{$column_width_margin}px;
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.eltdf-standard-layout {
		
		.eltdf-pli {
			float: left;
		}
		
		&.eltdf-normal-space {
			margin: 0 0 -30px;
		}
		
		&.eltdf-small-space {
			margin: 0 0 -20px;
		}
		
		&.eltdf-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.eltdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.eltdf-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pl-outer {
			opacity: 0;
		}
		
		.eltdf-pl-sizer {
			width: 100%;
		}
		
		.eltdf-pl-gutter {
			width: 0;
		}
		
		.eltdf-pli-inner {
			width: 100%;
		}
		
		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.eltdf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.eltdf-pli,
				.eltdf-pl-sizer {
					width: $column_width;
				}
				
				.eltdf-pli.eltdf-woo-image-large-width {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/

	/*************** Info On Image - start ***************/
	&.eltdf-info-on-image {

		.eltdf-pli-inner {

			.eltdf-pli-category {

				a {
					color: #fff;
				}
			}

			.eltdf-pli-text {
				text-align: center;
			}
		}
	}
	/*************** Info On Image  - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */